.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: relative;
  vertical-align: middle;
  box-sizing: border-box;
  user-select: none;
  background-color: transparent;
  outline: none;
  line-height: normal;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  -webkit-appearance: none;
  white-space: nowrap;
  overflow: hidden;
  letter-spacing: 0;
  text-align: center;
  border-width: 1px;
  border-style: solid;
  border-color: transparent;
  border-radius: 24px;
  height: 48px;
  text-transform: capitalize;
  padding: 0 30px; }
  .btn::-moz-focus-inner {
    padding: 0;
    border: 0; }
  .btn:before {
    content: " ";
    position: absolute;
    left: -1px;
    right: -1px;
    top: -1px;
    bottom: -1px;
    opacity: 0;
    background-color: #000;
    transition: opacity 0.2s ease-in-out; }
  .btn:hover:before {
    opacity: 0.04; }
  .btn:active:before {
    opacity: 0.16; }
  .btn:focus:before {
    opacity: 0.12; }
  .btn:active, .btn:focus {
    outline: none; }
  .btn--filled_primary {
    border-color: #722ed1;
    background-color: #722ed1;
    color: #fff; }
  .btn--filled_secondary {
    border-color: #fff;
    background-color: #fff;
    color: #000; }
  .btn--outlined {
    border-color: #722ed1;
    color: #722ed1; }
    .btn--outlined:hover {
      background-color: #722ed1;
      color: #fff; }
  .btn--google {
    padding: 0;
    border-radius: 4px; }
  .btn[type="submit"] {
